*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body{
  color: #000000;
  background: #4b7553;
  font-family: Lato, sans-serif;
  margin-top: 40px;
}

h1{
  color: #1b1b32;
  font-size: 25px;
  font-family: Roboto, sans-serif;
  text-align: center;
  margin: 20px 0;
}

.aplicacion-tareas{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.tareas-principal{
  width: 600px;
  min-height: 500px;
  background: #d0d0d0;
  padding: 25px;
  margin: 10px;
  border-radius: 26px;
}