.tarea-contenedor{
    width: 100%;
    min-height: 65px;
    background-color: #4f4fc7;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1px solid #fff;
    cursor: pointer;
}

.tarea-contenedor:nth-child(3n + 1){
    background: #d872d8;
}
.tarea-contenedor:nth-child(3n + 2){
    background: #f4fd71;
}
.tarea-contenedor:nth-child(3n + 3){
    background: #e69a29;
}
.tarea-contenedor:nth-child(3n + 4){
    background: #915740;
}

.tarea-texto{
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
}

.tarea-icono{
    width: 30px;
    height: 30px;
    margin: 5px;
    color: #ca0101;
}

.tarea-contenedor.completada{
    background-color: #1fda28;
    text-decoration: line-through;
}